import axios from "../../plugins/axios"

// 登录模块
export default {
    state: () => ({
        info: null
    }),
    mutations: {
        getInfo(state, info) {
            state.info = info
        }
    },
    actions: {
        // async getInfo({commit},token){
        //     const info = await axios.post(`/accesstoken`,{accesstoken: token})
        //     commit('getInfo',info)
        // }
        getInfo({ commit }, token) {
            return new Promise((resolve, reject) => {
                axios.post('/accesstoken', { accesstoken: token }).then(res => {
                    commit('getInfo', res)
                    localStorage.setItem('info', JSON.stringify(res))
                    localStorage.setItem('token', token)
                    resolve()
                }).catch(() => {
                    // console.log('出错了');
                    reject()
                })
            })
        }
    }
}