<template>
  <div v-if="post">
    <Penel>
      <template #content>
        <div class="post-header">
          <h2>
            <span class="tag" v-if="post.good || post.top">{{
              post.top ? "置顶" : "精华"
            }}</span>
            {{ post.title }}
          </h2>
          <p class="info">
            <span> · </span>
            <span>发布于 {{ post.last_reply_at | moment("from", "now") }}</span>
            <span> · </span>
            <span>作者 {{ post.author.loginname }}</span>
            <span> · </span>
            <span>{{ post.visit_count }} 次浏览</span>
            <span> · </span>
            <span>来自 {{ post.tab | transformTab }}</span>
          </p>
          <el-button @click="collect" size="mini" :type=" post.is_collect ? 'info' : 'success'" v-if="info">{{
              post.is_collect ? "取消收藏" : "收藏"
            }}</el-button>
        </div>
        <div class="post-content" v-html="post.content"></div>
      </template>
    </Penel>
    <Penel>
        <template #header>
            <span>{{post.reply_count}} 回复</span>
        </template>
        <template v-if="post.replies.length" #content>
            <Comment v-for="(comment,index) in post.replies" :key="comment.id" v-bind='{...comment,index}' />
        </template> 
    </Penel>
    <Penel>
        <template #header>添加回复</template>
        <template #content>
            <textarea v-model="content"></textarea>
            <span @click="add" class="reply">回复</span>
        </template>
    </Penel>
  </div>
</template>

<script>
import { mapActions, mapState } from "vuex";
import Penel from "../containers/Penel.vue";
import Comment from './Comment.vue';
export default {
  components: { Penel, Comment },
  data() {
    return {
      content: ''
    }
  },
  created() {
    this.getList(this.$route.params.id);
  },
  computed: {
    ...mapState({
      post: (state) => state.post.post,
      info: (state) => state.login.info
    }),
  },
  methods: {
    ...mapActions(["getList","collectHandle","addComment"]),
    collect(){
      this.collectHandle({id: this.post.id, is_collect: !this.post.is_collect})
    },
    add(){
      this.addComment({id:this.post.id, content:this.content})
    }
  },
};
</script>

<style lang='less'>
.post-header {
  .tag {
    background-color: #80bd01;
    color: #fff;
    padding: 2px;
    border-radius: 4px;
    font-size: 14px;
  }
  .info {
    color: #999;
  }
}
.post-content {
  img {
    width: 100%;
  }
}
.reply{
  cursor: pointer;
}
</style>