<template>
  <div class="create-post">
    <Penel>
      <template #header>
        <router-link to="/">首页</router-link><span> / </span
        ><span> 发布话题 </span>
      </template>
      <template #content>
        <el-form
          :model="ruleForm"
          :rules="rules"
          ref="ruleForm"
          label-width="100px"
          class="demo-ruleForm"
        >
          <el-form-item label="选择板块" prop="tab">
            <el-select v-model="ruleForm.tab" placeholder="请选择">
              <el-option label="分享" value="share"></el-option>
              <el-option label="问答" value="ask"></el-option>
              <el-option label="招聘" value="job"></el-option>
              <el-option label="客户端测试" value="dev"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item prop="title">
            <el-input v-model="ruleForm.title"></el-input>
          </el-form-item>

          <el-form-item prop="content">
            <el-input type="textarea" v-model="ruleForm.content"></el-input>
          </el-form-item>
        </el-form>
        <el-button @click="submit" type="primary">提交</el-button>
      </template>
    </Penel>
  </div>
</template>

<script>
import Penel from "../containers/Penel.vue";
import axios from '../plugins/axios'
export default {
  components: { Penel },
  data() {
    return {
      ruleForm: {
        tab: "",
        title: "",
        content: "",
      },
      rules: {
        tab: [{ required: true, message: "请选择", trigger: "change" }],
        title: [
          { required: true, message: "请输入活动名称", trigger: "blur" },
          { min: 10, message: "长度大于10个字符", trigger: "blur" },
        ],
        content: [{ required: true, message: "请输入内容", trigger: "blur" }],
      },
    };
  },
  methods: {
    async submit() {
        let {tab, title,content} = this.ruleForm
        const accesstoken = localStorage.getItem('token')
        const res = await axios.post('/topics',{tab,title,content,accesstoken})
        console.log(res);
        this.$router.push(`/post/${res.topic_id}`)
    },
  },
};
</script>

