import axios from "../../plugins/axios";
export default {
    state: () => ({
        user: null
    }),
    mutations:{
        getUser(state,post){
            state.user = post
        }
    },
    actions:{
        async getUser({commit},loginname){
            const res = await axios.get(`/user/${loginname}`)
            console.log(res);
            commit('getUser',res.data)
        }
    }
}