<template>
  <div class="post-list">
    <div v-if="list.length">
      <PostItem v-for="item in list" :key="item.id" v-bind="item" />
      <div v-if="loading" class="loading">加载中...</div>
    </div>
    <el-skeleton v-else :rows="18" animated />
  </div>
</template>

<script>
import { mapActions, mapState } from "vuex";
import PostItem from "../components/PostItem.vue";
import debounce from 'lodash/debounce'
export default {
    data() {
        return {
            // page: 2,
            loading:false
        }
    },
  components: { PostItem },
  watch: {
    "$route.params.tab": {
      handler() {
        this.getLists({tab:this.tab}); 
        this.page = 2;
      },
      immediate: true,
    },
  },

  computed: {
    ...mapState({
      list: (state) => state.postList.list,
    }),
    tab() {
      return this.$route.params.tab || "all";
    },
  },
  methods: {
    ...mapActions(["getLists"]),
    scrollFun: debounce(async function () {
      // console.log('滚动条事件触发了')
      // 获取到滚动条距离底部的距离
      // 滚动条距离顶部的距离 window.scrollY     document.documentElement.scrollTop
      // window 窗口的高度 window.innerHeight     document.documentElement.clientHeight
      // 文档的总高度 document.documentElement.scrollHeight
      const scrollHeight = document.documentElement.scrollHeight;
      const windowHeight =
        window.innerHeight || document.documentElement.clientHeight;
      const scrollTop = window.scrollY || document.documentElement.scrollTop;
      if (
        scrollHeight - windowHeight - scrollTop <= 200 &&
        this.list.length
      ) {
        // 因为进入页面的时候 当数据没来的时候，没有滚动条 默认可能会直接触发触底，但是并不想触发 判断就加了额外的条件
        // console.log('触底')
        if (!this.loading) {
          this.loading = true;
          this.page++;
          await this.getLists({ tab: this.tab, page: this.page });
          this.loading = false;
        }
      }
    }, 300),
  },
  mounted () {
    console.log(this.page);
      window.addEventListener("scroll", this.scrollFun);
  },
  beforeDestroy () {
      window.removeEventListener("scroll", this.scrollFun)
  },
};
</script>

