<template>
  <div class="comment" :id='id'>
    <router-link :to="`/user/${author.loginname}`">
    <el-avatar
      shape="square"
      size="medium"
      :src="author.avatar_url"
    ></el-avatar>
    </router-link>
    <div class="comment-right">
      <div>
        <span class="author_name">{{ author.loginname }} </span>
        <span class="position">
          <a :href="`#${id}`">
          {{ index + 1 }}楼 · {{ create_at | moment("from", "now") }}</a></span
        >
        <span @click="upsClick" v-if="ups.length" class="like">👍{{ ups.length ? ups.length : '' }}</span>
      </div>
      <div class="content" v-html="content"></div>
    </div>
  </div>
</template>

<script>
import { mapActions } from 'vuex';
export default {
  props: ["author", "index", "create_at", "content", "ups",'id','is_uped'],
  methods: {
    ...mapActions(['upsHandle']),
    upsClick(){
      this.upsHandle(this.id)
    }
  },
};
</script>

<style lang='less'>
.comment {
  display: flex;
  .comment-right {
    margin-left: 10px;
    flex-grow: 1;
    > div {
      display: flex;
      .position {
        color: #08c;
        flex-grow: 1;
      }
      .like{
        cursor: pointer;
      }
    }
    img {
      width: 100%;
    }
  }
}
</style>