<template>
  <div class="post-item">
      <router-link :to="`/user/${author.loginname}`">
    <el-avatar
      shape="square"
      size="medium"
      :src="author.avatar_url"
    ></el-avatar>
    </router-link>
    <div v-if="reply_count" class="count">
      <span class="reply_count">{{ reply_count }}</span>
      <span>/</span>
      <span class="visit_count">{{ visit_count }}</span>
    </div>
    <span v-if="isHasTag" :class="['tag', { active: top || good }]">{{
      tagText
    }}</span>

    <h3 class="title">
      <router-link :to="`/post/${id}`">{{ title }}</router-link>
    </h3>
    <span class="time">{{ last_reply_at | moment("from", "now") }}</span>
  </div>
</template>

<script>
import Vue from "vue";
export default {
  props: [
    "author",
    "reply_count",
    "visit_count",
    "top",
    "good",
    "title",
    "tab",
    "id",
    'last_reply_at'
  ],
  computed: {
    isHasTag() {
      const page = this.$route.path
      const isAllOrGoodPage = (page === "/all" || page === '') || page === "/good";
      return this.top ? true : isAllOrGoodPage ? true : false;
    },
    tagText() {
      return this.top
        ? "置顶"
        : this.good
        ? "精华"
        : Vue.filter("transformTab")(this.tab);
    },
  },
};
</script>

<style lang='less'>
.post-item {
  display: flex;
  align-items: center;
  .count {
    display: inline-block;
    font-size: 14px;
    margin-left: 8px;
    width: 80px;
    .reply_count {
      color: #9e78c0;
    }
  }
  h3 {
    font-size: 16px;
    font-weight: normal;
    flex-grow: 1;
    margin-left: 6px;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    a {
      color: #333;
      &:hover {
        text-decoration: underline;
      }
      &:visited {
        color: #999;
      }
    }
  }
  .tag {
    padding: 2px;
    font-size: 14px;
    border-radius: 4px;
    background-color: #e5e5e5;
    color: #999;
    margin-left: 5px;
    &.active {
      color: #fff;
      background-color: #80bd01;
    }
  }
}
</style>