<template>
  <div v-if="user" class="user">
    <Penel>
      <template #header>
        <span>主页</span>
      </template>
      <template #content>
        <div>
          <el-avatar
            shape="square"
            size="medium"
            :src="user.avatar_url"
          ></el-avatar>
          <span>{{ user.loginname }}</span>
        </div>
        <div>{{ user.score }} 积分</div>
        <div>
          github
          <a target="_blank" :href="`https://github.com/${user.githubUsername}`"
            >@{{ user.githubUsername }}</a
          >
        </div>
        <div class="time">
          注册时间 {{ user.create_at | moment("from", "now") }}
        </div>
      </template>
    </Penel>
    <Penel>
      <template #header>最近创建的话题</template>
      <template #content>
        <div v-if="recent_topics.length">
          <PostItem
            v-for="topic in recent_topics"
            :key="topic.id"
            v-bind="topic"
          />
        </div>
      </template>
    </Penel>
    <Penel>
      <template #header>最近参与的话题</template>
      <template #content>
        <div v-if="recent_replies.length">
          <PostItem
            v-for="topic in recent_replies"
            :key="topic.id"
            v-bind="topic"
          />
        </div>
      </template>
    </Penel>
  </div>
</template>

<script>
import { mapActions, mapState } from "vuex";
import PostItem from "../components/PostItem.vue";
import Penel from "../containers/Penel.vue";
import axios from "../plugins/axios";
export default {
  components: { Penel, PostItem },
  data() {
    return {
      recent_replies: [],
      recent_topics: [],
    };
  },
  computed: {
    ...mapState({
      user: (state) => state.user.user,
    }),
  },
  methods: {
    ...mapActions(["getUser"]),
  },
  created() {
      console.log(this.$route);
    // this.getUser(this.$route.params.userName);
  },
  watch: {
    "$route.params.userName": {
      handler(newValue) {
        this.getUser(newValue);
      },
      immediate: true,
    },
    user: {
      // 监听 user 的变化
      // 1. 首次进入 user 页，默认 user 是 null 然后 action 触发之后 user 更新了
      // 2. 当在当前页切换其他用户
      async handler(newValue) {
        // 要注意请求需要发多个而且要等待所有请求结束才能执行其他操作
        const res1 = await axios.all(
          newValue.recent_replies.map((topic) =>
            axios.get(`/topic/${topic.id}`)
          )
        );
        this.recent_replies = res1.map((ele) => ele.data);
        const res2 = await axios.all(
          newValue.recent_topics.map((topic) => axios.get(`/topic/${topic.id}`))
        );
        this.recent_topics = res2.map((ele) => ele.data);
      },
    },
  },
};
</script>

<style lang='less'>
.user {
  .time {
    padding-top: 20px;
    padding-bottom: 20px;
  }
}
</style>