<template>
  <div class="login">
    <Penel>
      <template #header>
        <router-link to="/">主页</router-link>
        <span>/</span>
        <span>登录</span>
      </template>
      <template #content>
        <el-form
          :model="ruleForm"
          hide-required-asterisk
          :rules="rules"
          ref="ruleForm"
          label-width="100px"
          class="demo-ruleForm"
        >
          <el-form-item label="用户名" prop="username">
            <el-input
              type="text"
              style="width: 260px"
              placeholder="用户名随便输"
              v-model="ruleForm.username"
              autocomplete="off"
            ></el-input>
          </el-form-item>
          <el-form-item label="密码" prop="password">
            <el-input
              type="password"
              placeholder="密码不能为空"
              style="width: 260px"
              v-model="ruleForm.password"
              autocomplete="off"
            ></el-input>
          </el-form-item>

          <el-form-item>
            <el-button type="primary" @click="login">登录</el-button>
          </el-form-item>
        </el-form>
      </template>
    </Penel>
  </div>
</template>

<script>
import { mapActions, mapState } from "vuex";
import Penel from "../containers/Penel.vue";
export default {
  components: { Penel },
  data() {
    return {
      ruleForm: {
        username: "",
        password: "b9280c90-d222-4a13-8af0-6ca7844f34f4",
      },
      rules: {
        username: [
          { required: true, trigger: "blur", message: "用户名不能为空" },
        ],
        password: [
          { required: true, trigger: "blur", message: "密码不能为空" },
        ],
      },
    };
  },
  computed: {
    ...mapState({
      info: (state) => state.login.info,
    }),
  },
  methods: {
    ...mapActions(["getInfo"]),

    login() {
      this.$refs.ruleForm.validate((boolean) => {
        if (boolean) {
          this.getInfo(this.ruleForm.password)
            .then(() => {
              this.$router.push("/");
            })
            .catch(() => {
              this.$message.error("错了哦，token有误");
            });
        }
      });
    },
  },
};
</script>

