// 文章列表模块
import axios from "../../plugins/axios";
export default {
    state: () => ({
        list: []
    }),
    mutations:{
        getLists(state,list){
            state.list = list
        },
        loadMore(state,list){
            state.list = [...state.list,...list]
        }
    },
    actions:{
        // 请求文章列表有两周情况
        // 1.初始请求
        // 2.加载更多请求
        // async getLists({commit},tab){
        //     const res = await axios.get(`/topics?tab=${tab}`)
        //     console.log(res.data);
        //     commit('getLists',res.data)
        // }
        async getLists({commit},query){
            const {page,tab} = query
            const queryStr = page ? `?tab=${tab}&page=${page}&limit=20` : `?tab=${tab}`
            console.log(queryStr);
            const res = await axios.get(`/topics/${queryStr}`)
            console.log(res.data);
            page ? commit('loadMore',res.data) : commit('getLists',res.data)
        }
    }
}